.page {
  font-family: ' Poppins ', sans-serif;
  width: 100%;
  color: $main-text-color;
  line-height: 140%;
  z-index: 1;
    &__body {
      background-color: $page-background-color;
      margin: 0;
    }

    &__menu {
      position: fixed;
      top: 0;
      right: 0;
      left: 0;

      opacity: 0;
      pointer-events: none;


      &:target {
        opacity: 1;
        pointer-events: all;
      }
    }

  &:has(.page__menu:target) {
  overflow: hidden;
  }
}

