.menu {
  block-size: border-box;
  overflow: auto;
  height: 100vh;
  background: $menu-background-color;
  font-weight: 600;
  text-transform: uppercase;


    &__top {
      margin-bottom: 24px;
      position: sticky;
      overflow: hidden;

    @include contentpadding;
    }

    &__nav {
      margin-bottom: 40px;
    }

    &__bottom {
      color: $menu-bottom-color;

      @include contentpadding;
    }

    &_phone_number {
      display: block;
      color: inherit ;
      text-decoration: none;
      font-size: 16px;
      line-height: 27px;
      margin-bottom: 16px;
      transition: transform 300ms;

         &:hover {
          display: block;
          border-bottom: 1px solid $menu-bottom-color;
          width: fit-content;
          color: inherit ;
          }
    }

    &_phone_to_order {
      display: block;
      padding-bottom: 7px;
      border-bottom: 1px solid $menu-bottom-color;
      width: fit-content;
      color: inherit ;
      text-decoration: none;
      font-size: 12px;
      line-height: 16px;
      letter-spacing: 2px;
      transition: transform 300ms;

        &:hover {
          display: block;
          border-bottom: 1px solid $menu-bottom-color;
          width: fit-content;
          color: inherit;
        }
    }
}
