.top-bar {
  padding: 24px 20px;
  display: flex;
  justify-content: space-between;
  align-items: center;

  @include contentpaddingtop;

    &__icons {
      display: flex;
      gap: 24px;
    }

    &__logo {
      width: 130px;
      height: 38px;
      display: flex;

      @include ontable {
        width: 187px;
        height: 56px;
      }
    }
}
