.nav {
  &__link {
    color: inherit ;
    text-decoration: none;
    font-size: 22px;
    line-height: 22px;
    letter-spacing: 2px;
    position: relative;

    &::after {
      position: absolute;
      content: '';
      display: block;
      border-bottom: 1px solid $menu-bottom-color;
      width: fit-content;
      width: 0;
    }
    &:hover::after {
      width: 100%;
    }
  }

  &__list {
    margin: 0;
    padding: 0;
    list-style: none;
    display: flex;
    flex-direction: column;
    gap: 32px;
  }

  &__item {
    display: inline-block;
    position: relative;

    // &::after {
    //   content: '';
    //   display: block;
    //   height: 1px;
    //   background-color: $menu-bottom-color;
    //   width: 0;
    // }
    // &:hover::after {
    //   width: 30%;
    // }
  }
}
